import React, { Component } from "react";
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Button,
  SwipeableDrawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Paper,
  Grid,
  Switch,
} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import CloseIcon from "@material-ui/icons/Close";
import PersonIcon from "@material-ui/icons/Person";
import PhoneIcon from "@material-ui/icons/Phone";
import CodeIcon from "@material-ui/icons/Code";
import FolderIcon from "@material-ui/icons/Folder";
import { getProjects } from "./Data";

class ApplicationBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isDrawerOpen: false,
      appBarTransparent: true,
      topLeftIconIsMenu: true,
      openProjects: false,
    };
  }

  toggleDrawer() {
    var oldState = this.state;
    this.setState({
      isDrawerOpen: !oldState.isDrawerOpen,
      appBarTransparent: !oldState.appBarTransparent,
      topLeftIconIsMenu: !oldState.topLeftIconIsMenu,
    });
  }

  menuItems = {
    home: { icon: <PersonIcon fontSize="large" />, displayName: "Profile" },
    contactMe: {
      icon: <PhoneIcon fontSize="large" />,
      displayName: "Contact Me",
    },
    projects: {
      icon: <FolderIcon fontSize="large" />,
      displayName: "Projects",
    },
  };

  render() {
    return (
      <div style={{ width: "100vw" }}>
        <AppBar
          color={this.state.appBarTransparent ? "transparent" : "#ffffff"}
          position="fixed"
          elevation={0}
        >
          <Grid container direction="column">
            <Grid item>
              <Toolbar>
                <CodeIcon
                  onClick={() => {
                    this.props.changeDisplayPage("home");
                  }}
                  style={{ cursor: "pointer" }}
                  fontSize="large"
                />
                <Typography style={{ flexGrow: 1 }} />

                <Typography variant="body2">Dark Mode</Typography>
                <Switch
                  checked={this.props.darkModeSwitchState}
                  onChange={() => this.props.toggleDarkMode()}
                ></Switch>
                {this.state.topLeftIconIsMenu ? (
                  <MenuIcon
                    onClick={() => this.toggleDrawer()}
                    style={{ cursor: "pointer" }}
                    fontSize="large"
                  />
                ) : (
                  <CloseIcon
                    onClick={() => this.toggleDrawer()}
                    style={{ cursor: "pointer" }}
                    fontSize="large"
                  />
                )}
              </Toolbar>
            </Grid>
            <Grid item container alignItems="center" jsutify="center">
              {this.state.isDrawerOpen && (
                <div
                  style={{
                    width: "100vw",
                    height: "100vh",
                  }}
                >
                  <List style={{ textAlign: "center" }}>
                    {Object.keys(this.menuItems).map((key) => {
                      return (
                        <ListItem
                          button
                          onClick={() => {
                            this.props.changeDisplayPage(key);
                            this.toggleDrawer();
                          }}
                        >
                          <ListItemIcon>
                            {this.menuItems[key]["icon"]}
                          </ListItemIcon>
                          <ListItemText
                            disableTypography
                            primary={
                              <Typography style={{ fontSize: "24px" }}>
                                {this.menuItems[key]["displayName"]}
                              </Typography>
                            }
                          />
                        </ListItem>
                      );
                    })}
                  </List>
                </div>
              )}
            </Grid>
          </Grid>
        </AppBar>
      </div>
    );
  }
}

export default ApplicationBar;
